body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: powderblue;
}

.popcicles {
  display: flex;
}

.pop-1 {
  position: relative;
  width: 28vw;
  height: 75vh;
}

.pop-1 > .layer {
  background-color: rgba(159, 227, 91, 0.8);
  width: 28vw;
  height: 48.75vh;
  position: absolute;
  z-index: 10;
  border-top-left-radius: 11.76vw 24.375vh;
  border-top-right-radius: 11.76vw 24.375vh;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
}

.pop-1 > .stick {
  background-color: #cdaa55;
  width: 5.6vw;
  height: 52.5vh;
  justify-self: center;
  position: absolute;
  border-radius: 30px;
  left: calc(50% - 2.8vw);
  bottom: calc(40% - 26.25vh);
}

.pop-2 {
  position: relative;
  width: 28vw;
  height: 75vh;
  margin-left: 5vw;
}

.pop-2 > .layer {
  background-color: linear-gradient(rgba(38, 224, 81, 0.8) 0%, rgba(38, 224, 81, 0.8) 33%, rgba(187, 110, 119, 0.8) 33%, rgba(187, 110, 119, 0.8) 66%, rgba(38, 135, 236, 0.8) 66%, rgba(38, 135, 236, 0.8) 100%);
  background-image: linear-gradient(rgba(38, 224, 81, 0.8) 0%, rgba(38, 224, 81, 0.8) 33%, rgba(187, 110, 119, 0.8) 33%, rgba(187, 110, 119, 0.8) 66%, rgba(38, 135, 236, 0.8) 66%, rgba(38, 135, 236, 0.8) 100%);
  width: 28vw;
  height: 48.75vh;
  position: absolute;
  z-index: 10;
  border-top-left-radius: 11.76vw 24.375vh;
  border-top-right-radius: 11.76vw 24.375vh;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
}

.pop-2 > .stick {
  background-color: #887316;
  width: 5.6vw;
  height: 52.5vh;
  justify-self: center;
  position: absolute;
  border-radius: 30px;
  right: calc(50% - 2.8vw);
  bottom: calc(40% - 26.25vh);
}